.hmi-header {
  height: 48px;
  padding: 0 !important;
  font-size: 14px; }
  .hmi-header ul {
    height: 38px; }
  .hmi-header b {
    font-weight: 500; }
  .hmi-header .icon-warning {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

.header-text {
  color: white; }

.hmi-header-list-item {
  width: 94px;
  position: relative; }
  .hmi-header-list-item:after {
    border-right: 1px solid #7088b7;
    content: "";
    right: 1px;
    top: 0;
    position: absolute;
    height: 38px;
    z-index: -1; }

.weighing-head-area {
  position: relative;
  display: flex;
  flex: 1; }
  .weighing-head-area ul {
    background: #ffffff;
    list-style-type: none;
    width: calc(100% + 4px);
    min-width: 260px;
    max-width: 400px;
    left: -2px;
    z-index: 1;
    top: -24px;
    position: absolute;
    height: 48px;
    padding: 0; }
    .weighing-head-area ul li {
      display: block;
      float: left;
      font-family: InterWeight;
      font-weight: 500;
      font-size: 42px;
      line-height: 48px;
      text-align: right;
      width: calc(100% - 66px - 40px); }
    .weighing-head-area ul .indicators {
      width: 66px;
      font-size: 60px;
      height: 48px; }
    .weighing-head-area ul .indicators div {
      float: left;
      height: 16px;
      width: 33px;
      font-size: 45px;
      line-height: normal;
      text-align: center;
      transform: translateY(-32px); }
    .weighing-head-area ul li .text {
      transform: translateY(-16px); }
    .weighing-head-area ul li .text,
    .weighing-head-area ul .unit {
      font-family: Inter;
      font-size: 12px;
      line-height: 48px; }
    .weighing-head-area ul .unit {
      text-align: right;
      padding-right: 10px;
      padding-top: 5px;
      width: 40px;
      font-size: 22px; }

.weighing-params-area > div {
  height: 48px;
  color: white;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  padding-right: 20px; }

.weighing-params-area .letter {
  font-size: 34px;
  line-height: 48px;
  font-weight: 600;
  padding-left: 30px; }

.weighing-params-area > div:last-child {
  padding-top: 5px;
  line-height: 19px;
  padding-right: 30px; }

.weighing-params-area .max-param {
  padding-top: 5px;
  line-height: 19px; }

.weighing-params-area .min-param {
