input[type="date"],
input[type="time"] {
  position: relative; }

input[type="date"]::-webkit-calendar-picker-indicator,
input[type="time"]::-webkit-calendar-picker-indicator {
  color: transparent;
  background: none;
  z-index: 1; }

input[type="date"]:before,
input[type="time"]:before {
  background: none; }

.switch {
  margin-right: 21px;
  margin-top: 10px; }

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 44px;
  height: 22px; }

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0; }

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s; }

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s; }

.cycle:before {
  position: absolute;
  content: "";
  height: 8px;
  width: 8px;
  left: 7px;
  bottom: 7px;
  background-color: white;
  border: 2px solid #9d9d9c;
  -webkit-transition: 0.4s;
  transition: 0.4s, border-width 0.1s;
  border-radius: 50%; }

input:checked + .slider {
  border-color: #585d60; }

input:checked + .slider + .cycle:before {
  background: white;
  border-color: #6f6f6f;
  width: 2px;
  border: 0;
  margin-left: 5px;
  border-radius: 0;
  -webkit-transform: translateX(19px);
  -ms-transform: translateX(19px);
  transform: translateX(19px); }

input:checked + .slider:before {
  background: #2c5095;
  border-color: #585d60;
  -webkit-transform: translateX(21px);
  -ms-transform: translateX(21px);
  transform: translateX(21px); }

input.readonly:checked + .slider:before {
  background: #2c5095cc; }

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
  border: 1px solid #9d9d9c; }

.slider.round:before {
  border: 1px solid #9d9d9c;
  border-radius: 50%; }

/*Disable states*/
input:disabled + .slider.round,
input:disabled + .slider.round:before,
input:disabled + .slider.round + .cycle:before {
  background-color: #f0f0f0;
  border-color: #989898;
  cursor: default; }

input.readonly + .slider.round {
  cursor: default; }

input:disabled + .slider.round {
  background-color: #d7d7d7; }

input:disabled:checked + .slider:before {
  background: #5f7091; }

